  font-size: 1.4rem;
  padding: 2px 0.5rem 0;
  border-radius: 2px;
}
@media only screen and (max-width: 767px) {
  .nav-control {
    right: -3rem;
  }
}
@media only screen and (max-width: 575px) {
  .nav-control {
