.sidebar {
  width: 17rem;
  top: 3rem;
  z-index: 1;
  background-color: #343957;
  box-shadow: 0 5px 20px rgba(0, 0, 0, 0.1);
  transition: all 0.2s ease;
  padding: 0 !important;
  color: #bdbdc7;
}
@media only screen and (min-width: 800px) and (max-width: 1350px) {
  .sidebar {
    width: 12.75rem;
  }
}
@media only screen and (min-width: 350px) and (max-width: 800px) {
  .sidebar {
    width: 100%;
  }
}
.sidebar .sidebar-menu {
  display: flex;
  flex-direction: column;
  overflow-y: auto;
  height: 95vh;
  /* ползунок скроллбара */
  /* Стрелки */
}
.sidebar .sidebar-menu ul {
  transition: all 0.2s ease-in-out;
}
.sidebar .sidebar-menu .nav-label {
  font-size: 0.75em;
  margin: 0 25px 0;
  padding: 1.5625rem 0 10px;
  text-transform: uppercase;
  letter-spacing: 0.05rem;
}
.sidebar .sidebar-menu .nav-item {
  display: block;
  padding: 0.8125rem 1.25rem;
  font-weight: 300;
  color: #bdbdc7;
  text-decoration: none;
}
.sidebar .sidebar-menu .nav-item + .nav-item {
  padding-left: 1.25rem;
}
.sidebar .sidebar-menu .nav-item > i {
  position: relative;
  font-size: 1.125rem;
  display: inline-block;
  vertical-align: middle;
  padding: 0 0.3rem;
  font-weight: 700;
  top: -2px;
}
.sidebar .sidebar-menu .nav-item:hover, .sidebar .sidebar-menu .nav-item:focus {
  background-color: #6b51df;
  color: #fff;
}
.sidebar .sidebar-menu .nav-item.active {
  color: #fff;
}
.sidebar .sidebar-menu .nav-text {
  padding: 0.8125rem 0.25rem;
  outline-width: 0;
}
.sidebar .sidebar-menu .has-arrow {
  position: relative;
}
.sidebar .sidebar-menu .has-arrow:after {
  position: absolute;
  content: "";
  width: 0.35em;
  height: 0.35em;
  border-width: 1px 0 0 1px;
  border-style: solid;
  border-color: initial;
  right: 1em;
  -webkit-transform: rotate(135deg) translateY(-50%);
  transform: rotate(135deg) translateY(-50%);
  -webkit-transform-origin: top;
  transform-origin: top;
  top: 48%;
  transition: all 0.3s ease-out;
}
.sidebar .sidebar-menu .has-arrow[aria-expanded=true]:after,
.sidebar .sidebar-menu .active > .has-arrow:after {
  -webkit-transform: rotate(225deg) translateY(-50%);
  transform: rotate(225deg) translateY(-50%);
}
.sidebar .sidebar-menu::-webkit-scrollbar {
  width: 3px;
  /* ширина для вертикального скролла */
  height: 1px;
  /* высота для горизонтального скролла */
}
.sidebar .sidebar-menu::-webkit-scrollbar-thumb {
  background-color: #9F3A3A;
  border-radius: 10em;
}
.sidebar .sidebar-menu::-webkit-scrollbar-button:vertical:start:decrement {
  display: none;
}
.sidebar .sidebar-menu::-webkit-scrollbar-button:vertical:end:increment {
  display: none;
}
.sidebar .sidebar-menu::-webkit-scrollbar-button:horizontal:start:decrement {
  display: none;
}
.sidebar .sidebar-menu::-webkit-scrollbar-button:horizontal:end:increment {
  display: none;
}

